@import "src/common/styles/variables";

.contactsBlock {
  min-height: 100vh;
  background-color: $bgColor;

  .formContainer {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    box-sizing: border-box;

    .formContacts {
      max-width: 700px;
      font-size: 16px;
      font-weight: 400;

      input {
        padding: 15px 10px;
        width: 100%;
        box-sizing: border-box;
        margin: 15px 1px;
        background-color: white;
        border: 1px solid #f2f2f2;
        border-radius: 3px;
        &:focus {
          border: 2px solid #00acc1;
          outline-offset: 0;
          outline: none;
        }
      }
      :nth-child(3){
        min-height: 100px;
        &::placeholder{
          //line-height: 20px;
          transform: translateY(-32.5px);
        }
      }


      textarea {
        padding: 15px 10px;
        height: 150px;
        width: 100%;
        box-sizing: border-box;
        margin: 15px 1px;
        background-color: white;
        border: 1px solid #f2f2f2;
        resize:none;
        &:focus {
          border: 2px solid #00acc1;
          outline-offset: 0;
          outline: none;
        }
      }
    }

    .buttonSend {
      padding: 13px 15px 13px 15px;
      text-transform: uppercase;
      margin-bottom: 30px;
      background-color: #00acc1;
      font-weight: 700;
      cursor: pointer;
      color: white;
      border-radius: 5px;
      border: none;

      &:hover {
        box-shadow: 0 3px 6px 0 RGB(0 0 0 / 40%);
      }
      &:disabled{
        background-color: #cccccc;
        box-shadow: inherit;
        cursor: inherit;
      }
    }
  }
}
